import * as THREE from 'three';
import { gsap } from 'gsap';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls';

import earthUrl from '../assets/imgs/earth.jpg';
import starsUrl from '../assets/imgs/stars.png';
import spriteUrl from '../assets/imgs/glow.png';
import moonUrl from '../assets/imgs/moon.jpg';
import lightUrl from '../assets/imgs/light_column.png';
import labelUrl from '../assets/imgs/label.png';

(function main() {
  const scene = new THREE.Scene();
  const camera = new THREE.PerspectiveCamera(
    45,
    window.innerWidth / window.innerHeight,
    0.1,
    100000
  );
  camera.position.set(0, 50, 300);
  scene.add(camera);

  const renderer = new THREE.WebGLRenderer({ antialias: true });
  renderer.setSize(window.innerWidth, window.innerHeight);

  document.body.appendChild(renderer.domElement);

  const controls = new OrbitControls(camera, renderer.domElement);
  controls.autoRotate = true;

  scene.background = new THREE.Color(0x030311);

  //vertex
  const vertices = [];
  const colors = [];
  for (let i = 0; i < 500; ++i) {
    let x = 800 * Math.random() - 400;
    let y = 800 * Math.random() - 400;
    let z = 800 * Math.random() - 400;
    vertices.push(x, y, z);
  }

  // starSky
  let starsGeometry = new THREE.BufferGeometry();
  starsGeometry.setAttribute(
    'position',
    new THREE.BufferAttribute(new Float32Array(vertices), 3)
  );

  const starsTexture = new THREE.TextureLoader().load(starsUrl);
  const starsMaterial = new THREE.PointsMaterial({
    size: 2,
    sizeAttenuation: true,
    color: 0x4d76cf,
    transparent: true,
    opacity: 1,
    map: starsTexture,
  });

  let stars = new THREE.Points(starsGeometry, starsMaterial);
  scene.add(stars);

  // earth
  // let earthGeometry = new THREE.SphereGeometry(50, 32, 32);
  // let earthTexture = new THREE.TextureLoader().load(earthUrl);
  // let earthMaterial = new THREE.MeshBasicMaterial({
  //   map: earthTexture
  // });
  // let earth = new THREE.Mesh(earthGeometry, earthMaterial);
  // scene.add(earth);

  // lighting earth
  let lightTexture = new THREE.TextureLoader().load(earthUrl);
  let lightEarthGeometry = new THREE.SphereGeometry(50, 32, 32);
  let lightEarthMaterial = new THREE.MeshBasicMaterial({
    map: lightTexture,
    alphaMap: lightTexture,
    blending: THREE.AdditiveBlending,
  });
  let lightEarth = new THREE.Mesh(lightEarthGeometry, lightEarthMaterial);
  scene.add(lightEarth);

  // lighting spirit
  let spriteTexture = new THREE.TextureLoader().load(spriteUrl);
  let spriteMaterial = new THREE.SpriteMaterial({
    map: spriteTexture,
    color: 0x4d76cf,
    transparent: true,
    depthWrite: false,
    depthTest: false,
    blending: THREE.AdditiveBlending,
  });
  let sprite = new THREE.Sprite(spriteMaterial);
  sprite.scale.set(155, 155, 0);
  scene.add(sprite);

  const lon2xyz = (R, longitude, latitude) => {
    let lon = (longitude * Math.PI) / 180; // 转弧度值
    const lat = (latitude * Math.PI) / 180; // 转弧度值
    lon = -lon; // js坐标系z坐标轴对应经度-90度，而不是90度

    // 经纬度坐标转球面坐标计算公式
    const x = R * Math.cos(lat) * Math.cos(lon);
    const y = R * Math.sin(lat);
    const z = R * Math.cos(lat) * Math.sin(lon);
    // 返回球面坐标
    return new THREE.Vector3(x, y, z);
  };

  for (let i = 0; i < 25; i++) {
    // 实现光柱
    let lightPillarTexture = new THREE.TextureLoader().load(lightUrl);
    let lightPillarGeometry = new THREE.PlaneGeometry(1, 20);
    let lightPillarMaterial = new THREE.MeshBasicMaterial({
      color: 0xffffff,
      map: lightPillarTexture,
      alphaMap: lightPillarTexture,
      transparent: true,
      blending: THREE.AdditiveBlending,
      side: THREE.DoubleSide,
      depthWrite: false,
    });
    let lightPillar = new THREE.Mesh(lightPillarGeometry, lightPillarMaterial);
    lightPillar.add(lightPillar.clone().rotateY(Math.PI / 2));

    // 创建波纹扩散效果
    let circlePlane = new THREE.PlaneGeometry(6, 6);
    let circleTexture = new THREE.TextureLoader().load(labelUrl);
    let circleMaterial = new THREE.MeshBasicMaterial({
      color: 0xffffff,
      map: circleTexture,
      transparent: true,
      blending: THREE.AdditiveBlending,
      depthWrite: false,
      side: THREE.DoubleSide,
    });
    let circleMesh = new THREE.Mesh(circlePlane, circleMaterial);
    circleMesh.rotation.x = -Math.PI / 2;
    circleMesh.position.set(0, -7, 0);

    lightPillar.add(circleMesh);

    gsap.to(circleMesh.scale, {
      duration: 1 + Math.random() * 0.5,
      x: 2,
      y: 2,
      z: 2,
      repeat: -1,
      delay: Math.random() * 0.5,
      yoyo: true,
      ease: "power2.inOut",
    });

    // 设置光柱的位置

    // lightPillar.position.set(0, 50, 0);

    let lat = Math.random() * 180 - 90;
    let lon = Math.random() * 360 - 180;
    let position = lon2xyz(60, lon, lat);
    lightPillar.position.set(position.x, position.y, position.z);

    lightPillar.quaternion.setFromUnitVectors(
      new THREE.Vector3(0, 1, 0),
      position.clone().normalize()
    );
    scene.add(lightPillar);
  }

  // moon
  let moonTexture = new THREE.TextureLoader().load(moonUrl);
  let moonMaterial = new THREE.MeshStandardMaterial({
    map: moonTexture,
    emissive: 0xffffff,
    emissiveMap: moonTexture,
  });
  let moonGeometry = new THREE.SphereGeometry(5, 32, 32);
  let moon = new THREE.Mesh(moonGeometry, moonMaterial);
  moon.position.set(150, 0, 0);
  scene.add(moon);

  // moon ring
  let moonRingTexture = new THREE.TextureLoader().load(moonUrl);
  let moonRingMaterial = new THREE.MeshBasicMaterial({
    map: moonRingTexture,
    transparent: true,
    blending: THREE.AdditiveBlending,
    side: THREE.DoubleSide,
    depthWrite: false,
    opacity: 0.2,
  });
  let moonRingGeometry = new THREE.RingGeometry(145, 155, 64);
  let moonRing = new THREE.Mesh(moonRingGeometry, moonRingMaterial);
  moonRing.rotation.x = -Math.PI/2;
  scene.add(moonRing);

  // auto rotate moon
  let time = {
    value: 0,
  };
  gsap.to(time, {
    value: 1, 
    duration: 10,
    repeat: -1,
    ease: 'linear',
    onUpdate: () => {
      moon.position.x = 150 * Math.cos(time.value * Math.PI * 2);
      moon.position.z = 150 * Math.sin(time.value * Math.PI * 2);
      moon.rotation.y = time.value * Math.PI * 8;
    }
  })

  THREE.DefaultLoadingManager.onProgress = function (item, loaded, total) {
    console.log(item, loaded, total);
    progress.value = new Number((loaded / total) * 100).toFixed(2);
  };

  // render
  function render() {
    controls.update();
    renderer.render(scene, camera);
    window.requestAnimationFrame(render);
  }

  render();

  window.addEventListener('resize', () => {
    camera.aspect = window.innerWidth / window.innerHeight;
    camera.updateProjectionMatrix();
    renderer.setSize(window.innerWidth, window.innerHeight);
  });
})();